import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom';

import { Provider } from 'react-redux';
import { combineReducers, createStore } from 'redux';

let hotBook = [{id:0}, {id:1}, {id:2}, {id:3}];
let newBook =[{id:0}, {id:1}, {id:2}, {id:3}];
let recentBook = [{id:0}, {id:1}, {id:2}, {id:3}];
let recommendBook = [{id:0}, {id:1}, {id:2}, {id:3}];

function reducer(state = hotBook, action) {

  switch (action.type) {
    case '':
      return state;
    default:
      return state
  }
}

function reducer2(state = newBook, action) {

  switch (action.type) {
    case '':
      return state;
    default:
      return state
  }
}

function reducer3(state = recentBook, action) {

  switch (action.type) {
    case '':
      return state;
    default:
      return state
  }
}

function reducer4(state = recommendBook, action) {

  switch (action.type) {
    case '':
      return state;
    default:
      return state
  }
}

let store = createStore(combineReducers({ reducer, reducer2, reducer3, reducer4 }));


ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
