import React from 'react';
import { CloseButton, Form, Button, Modal, Navbar, Nav, Tab, Tabs, Carousel, Card, Container, Row, Col, Media, Jumbotron } from 'react-bootstrap';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import './App.css';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBookReader, faBookOpen } from "@fortawesome/free-solid-svg-icons";

function TopNav() {
    return (
        <Nav className="top-nav pl-4 bg-dark text-light">
            <Container className="d-flex">
                <Nav.Item eventKey="novel" className="m-2">소설</Nav.Item>
                <Nav.Item eventKey="poet" className="m-2">시</Nav.Item>
                <Nav.Item eventKey="essay" className="m-2">에세이</Nav.Item>
                <Nav.Item eventKey="humanity" className="m-2">인문학</Nav.Item>
                <Nav.Item eventKey="classic" className="m-2">고전명작</Nav.Item>
                <Nav.Item eventKey="independent" className="m-2">독립출판</Nav.Item>
                <Nav.Item eventKey="collaborate" className="m-2 mr-4 ml-auto">제휴문의</Nav.Item>
            </Container>
        </Nav>
    )
}

function HeadNav(props) {
    return (
        <Row>
            <Col>
                <Navbar expand="lg">
                    <Navbar.Brand><Link to="/"><img src={process.env.PUBLIC_URL + '/img/logo.png'} width='100px' /></Link></Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="ml-auto nav-bar">
                            <Nav.Link href="#home" className="text-dark" >작가 등록</Nav.Link>
                            <Nav.Link className="text-dark" onClick={() => props.setModalShow(true)}>로그인</Nav.Link>
                            <Nav.Link as={Link} to="/signup" className="text-primary" >회원가입</Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </Col>
        </Row>
    )
}

function MyVerticallyCenteredModal(props) {
    return (
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header className="modal-header" closeButton>
                <Row style={{ justifyContent: "center", margin: 0, width: '100%' }}>
                    <Col className="col-md-6 p-0" style={{ overflow: 'hidden' }}>
                        <img src="https://placeimg.com/350/500/tech" />
                    </Col>
                    <Col className="mt-4 col-md-6">
                        <Modal.Title id="contained-modal-title-vcenter">
                            로그인
                        </Modal.Title>
                        <Form className="login-form-group">
                            <Form.Group controlId="formBasicEmail">
                                <Form.Control type="email" placeholder="이메일 주소를 입력해주세요" className="login-form" />
                            </Form.Group>

                            <Form.Group controlId="formBasicPassword">
                                <Form.Control type="password" placeholder="비밀번호를 입력해주세요" className="login-form" />
                            </Form.Group>
                            <Button variant="info" type="submit" className="login-form p-2">
                                로그인
                            </Button>
                            <Button variant="outline-success" type="submit" className="login-form p-2 mt-2" onClick={() => props.setModalShow(false)}>
                                <Link to="/Signup">
                                    회원가입
                                </Link>
                            </Button>
                        </Form>
                    </Col>
                </Row>
            </Modal.Header>
        </Modal>
    );
}

function MainCarousel(props) {
    return (
        <Row>
            <Col>
                <Carousel>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src="https://placeimg.com/500/150/tech"
                            alt="First slide"
                        />
                        <Carousel.Caption>
                            <h3>First slide label</h3>
                            <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src="https://placeimg.com/500/150/arch"
                            alt="Second slide"
                        />

                        <Carousel.Caption>
                            <h3>Second slide label</h3>
                            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src="https://placeimg.com/500/150/nature"
                            alt="Third slide"
                        />

                        <Carousel.Caption>
                            <h3>Third slide label</h3>
                            <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
                        </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>
            </Col>
        </Row>
    )
}

function NewBook(props) {
    return (
        <Row>
            {
                props.newBook.map(() => {
                    return (
                        <Col>
                            <Card style={{ border: 'none', height: '330px' }}>
                                <Card.Img variant="top" src="https://placeimg.com/100/70/tech" style={{ borderRadius: '5px' }} />
                                <Card.Body className="p-1 pt-3">
                                    <Card.Text style={{ fontSize: '10px' }} className="text-left mt-0">
                                        <Link to="/" style={{ color: '#666' }}>인문학</Link>
                                        <span style={{ color: '#666' }}> | </span>
                                        <Link to="/" style={{ color: '#666' }}>인문학</Link>
                                    </Card.Text>
                                    <Card.Title style={{ fontSize: '18px', fontWeight: 400 }}>지금 아는 걸 그때 알았더라면</Card.Title>
                                    <Card.Text className="text-right mt-0">
                                        <span style={{ color: '#aaa', fontSize: '13px', fontWeight: 400, textDecoration: 'line-through', textDecorationColor: '#aaa' }}>16,000원</span>
                                        <span style={{ fontSize: '16px', fontWeight: 700 }} className="ml-1">15,000원</span>
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </Col>
                    )
                })
            }
        </Row>
    )
}

