function RecommendBook(props) {
    return (
        <Row className="mt-5">
            <Col>
                <Tabs defaultActiveKey="MD" id="uncontrolled-tab-example" >
                    <Tab eventKey="MD" title={<div className="text-dark"><FontAwesomeIcon icon={faBookReader} className="fas fa-2x" /><p className="m-0 text-dark">MD추천</p></div>}>
                        <RecommendContent />
                    </Tab>
                    <Tab eventKey="novel" title={<div className="text-dark"><FontAwesomeIcon icon={faBookOpen} className="fas fa-2x" /><p className="m-0 text-dark black">소설</p></div>}>
                        <RecommendContent />
                    </Tab>
                    <Tab eventKey="poet" title={<div className="text-dark"><FontAwesomeIcon icon={faBookOpen} className="fas fa-2x" /><p className="m-0 text-dark black">시</p></div>}>
                        <RecommendContent />
                    </Tab>
                    <Tab eventKey="essay" title={<div className="text-dark"><FontAwesomeIcon icon={faBookOpen} className="fas fa-2x" /><p className="m-0 text-dark black">에세이</p></div>}>
                        <RecommendContent />
                    </Tab>
                    <Tab eventKey="humanity" title={<div className="text-dark"><FontAwesomeIcon icon={faBookOpen} className="fas fa-2x" /><p className="m-0 text-dark black">인문학</p></div>}>
                        <RecommendContent />
                    </Tab>
                    <Tab eventKey="classic" title={<div className="text-dark"><FontAwesomeIcon icon={faBookOpen} className="fas fa-2x" /><p className="m-0 text-dark black">고전명작</p></div>}>
                        <RecommendContent />
                    </Tab>
                    <Tab eventKey="independent" title={<div className="text-dark"><FontAwesomeIcon icon={faBookOpen} className="fas fa-2x" /><p className="m-0 text-dark black">독립출판</p></div>}>
                        <RecommendContent />
                    </Tab>
                </Tabs>
            </Col>
        </Row>
    )
}

