function ReduxState(state) {
    return {
        hotBook: state.reducer,
        newBook: state.reducer2,
        recentBook: state.reducer3,
        recommendBook: state.reducer4
    }
}


NewBook = connect(ReduxState)(NewBook);
HotBook = connect(ReduxState)(HotBook);
RecentBook = connect(ReduxState)(RecentBook);

export { TopNav, HeadNav, MainCarousel, NewBook, HotBook, RecentBook, RecommendBook, RecruitDesigner, FooterBar, MyVerticallyCenteredModal };